import React, { useState, useLayoutEffect } from 'react';
import logo from '../logo.svg';
import './Presentation.css';
import { Typography, Grow, Slide, Zoom, Hidden } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ArrowDownwardOutlined from '@material-ui/icons/ArrowDownwardOutlined'
import MailOutlineOutlined from '@material-ui/icons/MailOutlineOutlined'
import Const from '../helpers/Const'
import About from './About'
import Projects from './Projects'
import Contact from './Contact'

const useStyles = makeStyles(theme => ({
  lightFont: {
    fontWeight: 200,
  },
}));

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      setSize([window.innerWidth, window.innerHeight]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);
  return size;
}

function Presentation({ language, setLanguage }) {
  const classes = useStyles();
  const [mouseX, setMouseX] = useState(0)
  const [mouseY, setMouseY] = useState(0)
  const [section, setSection] = useState('index')
  const [shouldBackdropNav, setShouldBackdropNav] = useState(false)
  const [width, height] = useWindowSize();
  const isImgRotable = width > 960 && section === 'index'

  const labels = Const.labels[language]
  const topBarBackgroundThreshold = 30

  const handleSectionChange = (section) => {
    setSection(section)
    setShouldBackdropNav(false)
  }

  const handleScroll = e => {
    let element = e.target
    if (!shouldBackdropNav) {
      if (element.scrollTop > topBarBackgroundThreshold) {
        setShouldBackdropNav(true)
      }
    } else if (element.scrollTop < topBarBackgroundThreshold) {
      setShouldBackdropNav(false)
    }
  }

  const onMouseMove = (event) => {
    if (isImgRotable) {
      setMouseX(event.screenX)
      setMouseY(event.screenY)
    }
  }

  return (
    <div className="layout-section" onMouseMove={onMouseMove}>
      <PresentationImg isImgRotable={isImgRotable} mouseX={mouseX} mouseY={mouseY} width={width} height={height} isAboutMe={false && section === 'about'} />
      <div className="language-selection-div">
        <button onClick={() => setLanguage("es")} disabled={language == "es"} className={"button" + (language == "es" ? ' button-selected' : '')}><div className={"moving-underline" + (language == "es" ? ' moving-underline-active' : '')} /><Typography variant='body1' className={classes.lightFont}>ESPAÑOL</Typography></button>
        <button onClick={() => setLanguage("en")} disabled={language == "en"} className={"button" + (language == "en" ? ' button-selected' : '')}> <div className={"moving-underline" + (language == "en" ? ' moving-underline-active' : '')} /><Typography variant='body1' className={classes.lightFont}>ENGLISH</Typography></button>
      </div>
      {section !== 'projects' && <ContactVerticalBar />}
      <About labels={labels} show={section === 'about'} />
      <Projects show={section === 'projects'} handleScroll={handleScroll} labels={labels} />
      {/* <Contact language={language} show={section === 'contact'} handleScroll={handleScroll} /> */}

      <div className={"sections-container" + (shouldBackdropNav ? ' sections-container-backdrop-active' : '')}>
        <button onClick={() => setSection('index')} className={"button-back" + (section !== 'index' ? ' button-back-show' : '')}><ArrowDownwardOutlined fontSize="large" className="icon" /></button>
        <div>
          <button onClick={() => section === 'about' ? handleSectionChange('index') : handleSectionChange('about')} className={"button" + (section === "about" ? ' button-selected' : '')}><Typography variant='h6' className={classes.lightFont}>{labels.sectionTitles.about.toUpperCase()}</Typography></button>
          <button onClick={() => section === 'projects' ? handleSectionChange('index') : handleSectionChange('projects')} className={"button" + (section === "projects" ? ' button-selected' : '')}> <Typography variant='h6' className={classes.lightFont} >{labels.sectionTitles.projects.toUpperCase()}</Typography></button>
          {/* <button onClick={()=>handleSectionChange('contact')} className={"button" + (section === "contact" ? ' button-selected' : '')}> <Typography variant='h6' className={classes.lightFont}>{labels.sectionTitles.contact.toUpperCase()}</Typography></button> */}
        </div>
      </div>
      <div className='text-container'>
        <Grow in={section !== 'projects'} unmountOnExit timeout={{ enter: 500, exit: 500 }}>
          <div className='title-container'>
            <div className='title-name-container'>
              <Typography variant='h2' className={classes.lightFont}>LEANDRO ERCOLI</Typography>
            </div>
            <div className='title-container-separator' />
            <div className='specialities'>
              <Typography variant='h5' className='speciality'>{labels.titleDescription.toUpperCase()}</Typography>
              <Typography variant='h5' className='speciality'>{labels.titleDescriptionTwo.toUpperCase()}</Typography>
            </div>
          </div>
        </Grow>
      </div>
    </div>
  );
}

export default Presentation;

function PresentationImg({ isAboutMe, isImgRotable, mouseX, mouseY, width, height }) {
  let imgTransform = null
  let imgBoxShadow = null
  let imgTransition = null
  if (isAboutMe) {
    imgTransform = 'rotate3d(0,35, 0, 35deg) translateX(25%)'
    imgBoxShadow = '5px 5px 5px rgba(-35,-35,0,0.55)'
    imgTransition = 'all 0.5s ease-in-out'
  } else if (isImgRotable) {
    var centerX = Math.ceil(width / 2.0),
      centerY = Math.ceil(height / 2.0),
      dx = mouseX - centerX,
      dy = mouseY - centerY,
      tiltx = -(dy / centerY),
      tilty = (dx / centerX),
      radius = Math.sqrt(Math.pow(tiltx, 2) + Math.pow(tilty, 2)),
      degree = (radius * 15),
      shadx = -degree * tiltx,  /*horizontal shadow*/
      shady = -degree * tilty;   /*vertical shadow*/

    imgTransform = 'rotate3d(' + tiltx + ', ' + tilty + ', 0, ' + degree + 'deg)'
    imgBoxShadow = shady + 'px ' + (-shadx) + 'px 5px rgba(0,0,0,0.55)'
    imgTransition = 'all 0s ease-in-out'
  }

  return (
    <div className='img-container' style={{
      transform: imgTransform,
      boxShadow: imgBoxShadow,
      transition: imgTransition
    }}>
      <img src={require('../assets/IMG-20170409-WA0023-round.jpg')} className="img" alt="logo" />
      <div className="overlay-gradient" />
    </div>
  )
}

