import React, { useState, } from 'react';
import logo from '../logo.svg';
import './Section.css';
import './Projects.css';
import { Typography, Hidden } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CloseOutlined from '@material-ui/icons/CloseOutlined'
import NotesOutlined from '@material-ui/icons/NotesOutlined'

const useStyles = makeStyles(theme => ({
  lightFont: {
    fontWeight: 200,
  },
}));

function Projects({ labels, show, handleScroll }) {
  const classes = useStyles();
  const [activeIndex, setActiveIndex] = useState(-1)

  const projects = labels.projects

  const handleProjectClick = (event, index) => {
    event.stopPropagation();
    setActiveIndex(index);
  }
  const handleBackdropClick = () => { activeIndex > -1 && setActiveIndex(-1) }

  return (
    <div className={"container container-projects" + (show ? ' container-show' : '')} onClick={handleBackdropClick}>
      <div className="projects-grid" onScroll={handleScroll} >
        {
          projects.map((project, index) => (
            <div className={"project-container" + (index === activeIndex ? ' project-container-active' : '')} key={index} onClick={(event) => handleProjectClick(event, index)}>
              <div className='project-img-container'>
                <img src={project.img} className="img" alt="logo" />
              </div>
              <div className="project-text-container">
                <RenderProjectTitulos project={project} />
                {(index !== activeIndex) && <Hidden smUp>
                  <button onClick={(event) => { event.stopPropagation(); handleProjectClick(event, index) }} className='more-button'><NotesOutlined fontSize='large' style={{ color: '#fff' }} /> </button>
                </Hidden>}
                {(index === activeIndex) && <div className="bottom-text">
                  <Typography variant='h6' align='justify' className={classes.lightFont} style={{margin: '0 5%', paddingBottom:'15px'}}>{project.descripcion}</Typography>
                </div>}
                {(index === activeIndex) && <button onClick={(event) => { event.stopPropagation(); setActiveIndex(-1) }} className='close-button'><CloseOutlined fontSize='large' style={{ color: '#fff' }} /> </button>}
              </div>
            </div>
          ))
        }
      </div>
    </div>
  );
}

export default Projects;

const RenderProjectTitulos = ({ project }) => {
  const classes = useStyles();
  return (
    <div className="top-text">
      <Typography variant='h3' align='center' className={classes.lightFont}>{project.titulo.toUpperCase()}</Typography>
      <Typography variant='h6' align='center' className={classes.lightFont}>{project.subtitulo.toUpperCase()}</Typography>
      <div className="project-links">
        {
          project.links.map((link, indexLink) => (
            <div className="link" key={'link-icon' + indexLink}>
              <a href={link.link} target="_blank" rel="noopener noreferrer" >{link.icon}</a>
            </div>
          ))
        }
      </div>
    </div>
  )
}