import React, { useState, useEffect } from 'react';
import MuiThemeProvider from "@material-ui/core/styles/MuiThemeProvider";
import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";
import Presentation from './Layouts/Presentation'

const THEME = createMuiTheme({
  typography: {
    "fontFamily": "\"Dosis\", sans-serif",
    "fontSize": 14,
    "fontWeightLight": 200,
    "fontWeightRegular": 400,
    "fontWeightMedium": 500
  }
});

function App() {
  const [language, setLanguage] = useState("es")

  useEffect(() => {
    var userLang = navigator.language || navigator.userLanguage
    const language = (userLang && ["en", "en-US", "en-EG", "en-AU", "en-GB", "en-CA", "en-NZ", "en-IE", "en-ZA", "en-JM", "en-BZ", "en-TT"]
      .includes(userLang)) ?
      "en"
      // : (["es", "es-AR", "es-GT", "es-CR", "es-PA", "es-DO", "es-MX", "es-VE", "es-CO",
      //   "es-PE", "es-EC", "es-CL", "es-UY", "es-PY", "es-BO", "es-SV", "es-HN", "es-NI",
      //   "es-PR"].includes(userLang)) ?
      : "es"
    setLanguage(language)
  }, [])

  return (
    <MuiThemeProvider theme={responsiveFontSizes(THEME)}>
      <Presentation lang={'es'} language={language} setLanguage={setLanguage}/>
    </MuiThemeProvider>
  );
}

export default App;
